function Loader() {
    'use strict';
    return this;
}
Loader.prototype = {
    loadImages: function (fileList, step, callback) {
        'use strict';
        var self = this,
            arrLength = fileList.length,
            result = [];

        $.each(fileList, function (key, value) {
            var _self = self.msie ? $('<img/>')[0] : new Image(),
                src = this.fileName,
                fileSize = this.fileSize;

            function ready(){
                var elem = {
                    src: $(_self).attr('src'),
                    width: $(_self)[0].naturalWidth,
                    height: $(_self)[0].naturalHeight
                };
                result.push(elem);
                arrLength--;
                if (typeof step === 'function') {
                    step(fileSize, _self, elem, key);
                }
                if (!arrLength && typeof callback === 'function') {
                    callback(result);
                }
            };
            function err(){
                var elem = {
                    src: $(_self).attr('src'),
                    width: $(_self)[0].naturalWidth,
                    height: $(_self)[0].naturalHeight
                };
                result.push(elem);
                arrLength--;
                if (typeof step === 'function') {
                    step(fileSize, _self, elem, key);
                }
                if (!arrLength && typeof callback === 'function') {
                    callback(result);
                }
            };

            src = src.replace('../img/', 'img/');
            src = src.replace('../pic/', 'pic/');

            if (typeof value === 'object') {

                $(_self).load(function () {
                    ready();
                    $(_self).remove();
                }).error(function () {
                    err();
                    $(_self).remove();
                }).attr('src', src);
            }
        });
    },

    silentLoadImages: function (fileList, step, callback, slide) {
        'use strict';
        var self = this,
            arrLength = fileList.length,
            result = [];

        $.each(fileList, function (key, value) {
            var _self = self.msie ? $('<img/>')[0] : new Image(),
                src = this.fileName,
                fileSize = this.fileSize;

            function ready(){
                arrLength--;
                if (typeof step === 'function') {
                    step();
                }
                if (!arrLength && typeof callback === 'function') {
                    callback(slide);
                }
            };
            function err(){
                arrLength--;
                if (typeof step === 'function') {
                    step();
                }
                if (!arrLength && typeof callback === 'function') {
                    callback(slide);
                }
            };

            src = src.replace('../img/', 'img/');
            src = src.replace('../pic/', 'pic/');

            if (typeof value === 'object') {
                $(_self).load(function () {
                    ready();
                    $(_self).remove();
                }).error(function () {
                    err();
                    $(_self).remove();
                }).attr('src', src);
            }
        });
    },

    objectLength: function (obj) {
        'use strict';
        var self = this,
            result = 0;

        $.each(obj, function (key, value) {
            result++;
        });

        return result;
    }
};